.sideBar {
    position: fixed;
    display: block;
    visibility: visible;
    width: 22%;
    height: 100%;
    background-color: #d8e1e7;
    overflow: auto;
}

.profile-img {
    position: relative;
    margin: 30px auto 20px;
    text-align: center;
    z-index: 0;
}

.profile-img img {
    position: relative;
    border-radius: 200px;
    width: 150px;
}

.site-title-block {
    position: relative;
    z-index: 1;
    margin: 20px 20px 10px;
    text-align: center;
}

.site-title {
    color: #222;
    font-size: 30px;
    font-weight: 500;
    line-height: 1.1em;
    margin: 15px 0 0 0;
    text-transform: uppercase;
}

p.site-description {
    color: #522d2d;
    font-size: 16px;
    font-weight: 300;
    font-family: Oswald, Helvetica, sans-serif;
    margin: 10px 0;
}

.site-nav {
    display: block;
    padding: 25px 0 30px;
}

.site-main-menu {
    display: block;
    padding: 0;
    margin: 0;
    font-family: Oswald, Helvetica, sans-serif;
}

.site-main-menu li {
    display: block;
    position: relative;
    padding: 0;
    text-align: center;
    cursor: pointer;
}

.site-main-menu li a{
    display: block;
    position: relative;
    padding: 10px 20px;
    text-align: center;
    color: #222;
    font-size: 22px;
    font-weight: 500;
    position: relative;
    text-decoration: none;
    border-bottom: 1px solid #f5f5f5;
    outline: none;
}

.site-main-menu li:first-child a {
    border-top: 1px solid #f5f5f5;
}

.site-main-menu li a:hover {
    font-weight: 800;
    color: white;
}

.hvr-sweep-to-right {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    -webkit-transition-property: color;
    transition-property: color;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
}

.hvr-sweep-to-right:before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-right: 5px solid #ff4500;
    background-color: #4a4a4a;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transform-origin: 0 50%;
    transform-origin: 0 50%;
    -webkit-transition-property: transform;
    transition-property: transform;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
}

.hvr-sweep-to-right:hover,
.hvr-sweep-to-right:focus,
.hvr-sweep-to-right:active {
    color: #ffffff;
}

.hvr-sweep-to-right:hover:before,
.hvr-sweep-to-right:focus:before,
.hvr-sweep-to-right:active:before {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
}

.sectionShow {
    display: block;
}

.sectionHide {
    display: none;
}

.clearfix {
    overflow: auto;
    clear: both;
}

.navFooter {
    position: relative;
    bottom: 0px;
    font-size: 14px;
    padding-left: 30px;
    background-color: #d8bfd8;
    display: block;
    padding-top: 10px;
    padding-bottom: 10px;
}

.mobmenu__div {
    cursor: pointer;
    float: right;
    padding: 8px 20px;
}

.bar1,
.bar2,
.bar3 {
    width: 48px;
    height: 6px;
    background-color: #333;
    margin: 5px 0;
    transition: 0.4s;
}

.change .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px);
}

.change .bar2 {
    opacity: 0;
}

.change .bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
    transform: rotate(45deg) translate(-8px, -8px);
}

.mobileNav {
    display: none;
    visibility: hidden;
}

.mobileNav h1 {
    float: left;
    padding: 7px 16px;
    font-size: 30px;
}

.dateTime {
    position: absolute;
    left: 4%;
    top: 3%;
    font-size: 17px;
}

p.DateTimeHide {
    display: block;
}

/* media query */

@media (max-width: 1200px) {
    .mobileNav {
        display: none;
        visibility: hidden;
    }

    p.DateTimeHide {
        display: block;
    }

    .navFooter {
        font-size: 14px;
        padding-left: 10px;
        position: static;
    }
}

@media (max-width: 992px) {

    p.DateTimeHide {
        display: none;
    }

    .sideBar {
        width: 100%;
        max-width: 270px;
        height: calc(100% - 50px);
        top: 50px;
        z-index: 999;
        -webkit-transition: all 0.66s ease-in-out;
        -moz-transition: all 0.66s ease-in-out;
        -o-transition: all 0.66s ease-in-out;
        -ms-transition: all 0.66s ease-in-out;
        transition: all 0.66s ease-in-out;
    }

    .mobileNav {
        display: block;
        visibility: visible;
        width: 100%;
        height: 50px;
        background-color: #f5f5f5;
    }

    .mobNavShow {
        margin-left: 0px;
    }

    .mobNavHide {
        margin-left: -320px;
    }

    .navFooter {
        display: none;
    }

}

@media (max-width: 768px) {
    .sideBar {
        max-width: 230px;
    }

    .profile-img img {
        width: 130px;
    }

    .site-title {
        font-size: 22px;
    }

    .site-nav {
        padding: 15px 0 30px;
    }

}

@media (max-width: 576px) {
    .sideBar {
        max-width: 185px;
    }

    .site-title {
        font-size: 18px;
    }

    .profile-img img {
        width: 110px;
    }

    .site-main-menu li a{
        font-size: 18px;
    }

}

/* media query */