.sliderBorder{
    border: 2px solid black;
    height: 15px;
    border-radius: 30px;
    margin-bottom: 20px;
}

.akshatSkillsBg{
    background-color: #ffdead;
}

.skillsTitle{
    margin-bottom: 10px;
    font-size: 22px;
}

.sliderIn{
    background-color: #f50505;
    height: 15px;
    border-radius: inherit;
    transition: all 0.9s ease-in-out;
}

.sliderIn.skillHtml{
    width:95%;
}

.sliderIn.skillCss{
    width:85%;
}

.sliderIn.skillJs{
    width:78%;
}

.sliderIn.skillBs{
    width:93%;
}

.sliderIn.skillReact{
    width:65%;
}

.sliderIn.skillNode{
    width:55%;
}

.sliderIn.skillPs{
    width:50%;
}