.main__div{
    background-color: aqua;
    padding: 70px 40px;
}

.container {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
}

.myaudio{
    width: 30%;
    border: 2px dashed red;
    border-radius: 30px;
    position: fixed;
}

.musicList{
    cursor: pointer;
    padding: 18px;
    font-size: 25px;
    border: 1px solid red;
}

.music__row{
    display: flex;
    flex-wrap: wrap;
}

.music__leftDiv{
    flex:0 0 50%;
}

.music__rightDiv{
    flex:0 0 50%;
}

.music__mainlist{
    width:60%;
}

@media (max-width:1200px) {

    .music__leftDiv{
        flex:0 0 50%;
    }
    
    .music__rightDiv{
        flex:0 0 50%;
    }

}

@media (max-width:992px) {

    .music__leftDiv{
        flex:0 0 50%;
    }
    
    .music__rightDiv{
        flex:0 0 50%;
    }

}

@media (max-width:768px) {

    .music__leftDiv{
        flex:0 0 100%;
    }
    
    .music__rightDiv{
        flex:0 0 100%;
    }

}

@media (max-width:576px) {

    .music__mainlist {
        width: 90%;
    }

    .music__leftDiv{
        flex:0 0 100%;
    }
    
    .music__rightDiv{
        flex:0 0 100%;
    }

    .myaudio {
        width: 70%;
        bottom: 0;
        
    }

}